@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  background-image: -webkit-gradient(linear, left top, right top, from(#8f94fb), to(#4e54c8));
  background-image: -webkit-linear-gradient(left, #8f94fb, #4e54c8);
  background-image: linear-gradient(to right, #8f94fb, #4e54c8);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }

.container {
  background: #27282d;
  height: 900px;
  width: 900px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 4px 30px -5px rgba(0, 0, 0, 0.65);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  padding: 30px; }

.menu-item {
  background: #27282d;
  padding: 10px !important;
  border-radius: 10px !important;
  display: flex !important;
  color: #ccced3 !important; }

.menu-item:hover {
  font-weight: bold;
  color: #626571; }

.content {
  padding: 60px;
  height: inherit;
  width: inherit; }

.input-container {
  margin-bottom: 20px;
  width: 700px; }

.item {
  color: #ccced3 !important;
  padding-right: 30px;
  padding-left: 30px;
  text-align: justify;
  background: inherit !important; }

.header {
  color: #ccced3;
  padding-right: 30px;
  padding-left: 30px; }

input, button {
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.8px;
  margin: 20px; }

button {
  background: #33343b;
  border: none;
  border-radius: 30px;
  color: #ccced3;
  font-size: 12px;
  padding: 16px 36px;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: -30px;
  box-shadow: 0px 4px 15px -5px rgba(0, 0, 0, 0.65);
  -webkit-transition: all .3s;
  transition: all .3s;
  outline: none; }

button:hover {
  outline-style: none;
  border-radius: 30px;
  background: #3f4048;
  box-shadow: 0px 5px 20px -3px rgba(0, 0, 0, 0.65); }

@media screen and (max-width: 620px) {
  .container {
    min-height: 300px;
    max-height: 420px;
    width: 90vw; }
  .input-container {
    width: 80%;
    margin-bottom: 40px; }
  button {
    margin-top: 10px;
    margin-bottom: -20px; } }

